import { render } from "@headlessui/react/dist/utils/render";
import React, { Component } from "react";
import { GW2API_Call } from "~/GW2Api/GW2API_Call";

interface ItemCardProps{
    apikey:string;
    itemid:string;
}

interface ItemCardState{
    isLoading:boolean;
    response:String;
    gw2apicall: GW2API_Call;

}

class ItemCard extends Component<ItemCardProps, ItemCardState>{


    constructor(props: ItemCardProps){
        super(props);
        this.state = {
            isLoading: true,
            response: "",
            gw2apicall: new GW2API_Call(props.apikey)
        }
    }

    render(){
    return(
        <>
            {this.state.gw2apicall.IsDone ? this.state.gw2apicall.Response : "DoneLoading"}
        </>
    );
    }
}
   


export default ItemCard;